export interface Project {
  slug: string;
  title: string;
  date: string;
  tags: string[];
  technologies: string[];
  company: string;
  thumbnail: string;
  featured: boolean;
  excerpt: string;
  content: string;
}

export const loadProjects = async (): Promise<Project[]> => {
  const projects = await import('../content/projects.json');
  return projects.default;
};

export const getProjectBySlug = async (slug: string): Promise<Project | null> => {
  const projects = await loadProjects();
  return projects.find((project) => project.slug === slug) || null;
};

export const getFeaturedProjects = async (): Promise<Project[]> => {
  const projects = await loadProjects();
  return projects.filter((project) => project.featured);
};

export const getProjectsByTag = async (tag: string): Promise<Project[]> => {
  const projects = await loadProjects();
  return projects.filter((project) => project.tags.includes(tag));
};

export const getProjectsByTechnology = async (tech: string): Promise<Project[]> => {
  const projects = await loadProjects();
  return projects.filter((project) => project.technologies.includes(tech));
};
