import React, { useEffect, useState } from 'react';
import {
  Container,
  Typography,
  Box,
  Chip,
  IconButton,
  Paper,
} from '@mui/material';
import { motion } from 'framer-motion';
import { useParams, useNavigate } from 'react-router-dom';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ReactMarkdown from 'react-markdown';
import { Project as ProjectType, getProjectBySlug } from '../utils/projects';

export const Project = () => {
  const { slug } = useParams<{ slug: string }>();
  const navigate = useNavigate();
  const [project, setProject] = useState<ProjectType | null>(null);

  useEffect(() => {
    if (slug) {
      getProjectBySlug(slug).then(setProject);
    }
  }, [slug]);

  if (!project) {
    return (
      <Container maxWidth="lg">
        <Box sx={{ py: 4 }}>
          <Typography variant="h2">Project not found</Typography>
        </Box>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg">
      <Box sx={{ py: 4 }}>
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          <Box sx={{ mb: 4, display: 'flex', alignItems: 'center' }}>
            <IconButton
              onClick={() => navigate('/portfolio')}
              sx={{ mr: 2 }}
              aria-label="back to portfolio"
            >
              <ArrowBackIcon />
            </IconButton>
            <Typography
              variant="h1"
              sx={{
                fontSize: { xs: '2rem', md: '3rem' },
                fontWeight: 700,
                background: 'linear-gradient(45deg, #000000 30%, #1d1d1f 90%)',
                WebkitBackgroundClip: 'text',
                WebkitTextFillColor: 'transparent',
              }}
            >
              {project.title}
            </Typography>
          </Box>

          <Box sx={{ mb: 4 }}>
            <Typography
              variant="subtitle1"
              color="text.secondary"
              sx={{ mb: 2 }}
            >
              {project.company} • {project.date}
            </Typography>

            <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap', mb: 3 }}>
              {project.technologies.map((tech) => (
                <Chip
                  key={tech}
                  label={tech}
                  sx={{
                    backgroundColor: 'rgba(0, 0, 0, 0.08)',
                    '&:hover': {
                      backgroundColor: 'rgba(0, 0, 0, 0.12)',
                    },
                  }}
                />
              ))}
            </Box>

            <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap', mb: 4 }}>
              {project.tags.map((tag) => (
                <Chip
                  key={tag}
                  label={tag}
                  variant="outlined"
                  sx={{
                    borderColor: 'rgba(0, 0, 0, 0.23)',
                    '&:hover': {
                      backgroundColor: 'rgba(0, 0, 0, 0.04)',
                    },
                  }}
                />
              ))}
            </Box>

            {project.thumbnail && (
              <Box
                component="img"
                src={project.thumbnail}
                alt={project.title}
                sx={{
                  width: '100%',
                  height: 'auto',
                  maxHeight: '500px',
                  objectFit: 'cover',
                  borderRadius: 2,
                  mb: 4,
                }}
              />
            )}
          </Box>

          <Paper elevation={2} sx={{ p: 4 }}>
            <ReactMarkdown
              components={{
                h1: ({ children }) => (
                  <Typography variant="h2" gutterBottom>
                    {children}
                  </Typography>
                ),
                h2: ({ children }) => (
                  <Typography variant="h3" gutterBottom sx={{ mt: 4 }}>
                    {children}
                  </Typography>
                ),
                p: ({ children }) => (
                  <Typography variant="body1" paragraph>
                    {children}
                  </Typography>
                ),
                ul: ({ children }) => (
                  <Box component="ul" sx={{ pl: 2 }}>
                    {children}
                  </Box>
                ),
                li: ({ children }) => (
                  <Typography component="li" sx={{ mb: 1 }}>
                    {children}
                  </Typography>
                ),
              }}
            >
              {project.content}
            </ReactMarkdown>
          </Paper>
        </motion.div>
      </Box>
    </Container>
  );
};
