import React from 'react';
import { Box, AppBar, Toolbar, Button, Container, useScrollTrigger } from '@mui/material';
import { motion } from 'framer-motion';
import { Link as RouterLink } from 'react-router-dom';

const NavButton = ({ to, children }: { to: string; children: React.ReactNode }) => (
  <Button
    component={RouterLink}
    to={to}
    sx={{
      color: 'text.primary',
      '&:hover': {
        backgroundColor: 'transparent',
        opacity: 0.7,
      },
    }}
  >
    {children}
  </Button>
);

export const Layout = ({ children }: { children: React.ReactNode }) => {
  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 0,
  });

  return (
    <Box>
      <AppBar
        position="fixed"
        elevation={0}
        sx={{
          backgroundColor: trigger ? 'rgba(255, 255, 255, 0.8)' : 'transparent',
          backdropFilter: trigger ? 'blur(20px)' : 'none',
          borderBottom: trigger ? '1px solid rgba(0, 0, 0, 0.1)' : 'none',
          transition: 'all 0.3s ease',
        }}
      >
        <Container maxWidth="lg">
          <Toolbar sx={{ justifyContent: 'space-between' }}>
            <Button
              component={RouterLink}
              to="/"
              sx={{
                fontWeight: 700,
                color: 'text.primary',
                '&:hover': {
                  backgroundColor: 'transparent',
                  opacity: 0.7,
                },
              }}
            >
              Ryan Sander
            </Button>
            <Box sx={{ display: 'flex', gap: 2 }}>
              <NavButton to="/about">About</NavButton>
              <NavButton to="/portfolio">Portfolio</NavButton>
              <NavButton to="/blog">Blog</NavButton>
              <NavButton to="/contact">Contact</NavButton>
            </Box>
          </Toolbar>
        </Container>
      </AppBar>
      <Toolbar /> {/* Spacer */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <Container maxWidth="lg" sx={{ py: 4 }}>
          {children}
        </Container>
      </motion.div>
    </Box>
  );
};
